import React from 'react';
import PropTypes from 'prop-types';
import ProductItem from '../ProductItem/ProductItem';

const Products = ({ data }) => (
  <ul className="products">
    {data.map((item) => (
      <ProductItem data={item} />
    ))}
  </ul>
);

Products.propTypes = {
  data: PropTypes.string.isRequired,
};

export default Products;
